.Colorbox {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	/*justify-content: center;*/
	-ms-flex-align: center;
	    align-items: center;
	margin: 1rem;
}

.Colorbox-divider {
	border-left: 1px solid rgba(0, 0, 0, 0.5);
	margin: 0 8px;
	height: 42px;
}

.Colorbox-selected-swatch {
	margin: 2px;
	width: 42px;
	height: 42px;
	min-width: 0 !important;
}



